.footer {
  padding-top: 44px;
  padding-bottom: 50px;

  background-color: $black-russian;

  @media (max-width: $tablet-size-max) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @media (max-width: $mobile-size-max) {
    padding-top: 50px;
    padding-bottom: 31px;
  }
}

.footer__wrapper {
  display: grid;
  // grid-template-columns: auto 1fr auto;

  grid-template-columns: repeat(4, auto);

  @media (max-width: $mobile-size-max) {
    column-gap: 70px;

    grid-template-columns: auto 1fr;
    row-gap: 30px;
  }
}

.footer__logo {
  margin-top: 0;
  // margin-right: 276px;
  margin-right: 178px;

  @media (max-width: $tablet-size-max) {
    margin-right: 62px;
  }

  @media (max-width: $mobile-size-max) {
    margin-right: 0;
  }
}

.footer__social-list {
  display: grid;
  // justify-content: start;
  max-width: 113px;
  margin-left: 15px;
  // gap: 12px;

  grid-template-columns: repeat(3, auto);

  @media (max-width: $tablet-size-max) {
    max-width: 90px;
    // margin-top: 10px;
  }

  @media (max-width: $mobile-size-max) {
    // justify-content: center;
    max-width: none;
    margin-left: 0;
  }
}

.footer__social-item {
  margin-bottom: 10px;
}

.footer__social-item:not(:last-child) {
  margin-right: 11px;

  // @media (max-width: $tablet-size-max) {
  //   margin-right: 19px;
  // }

  // @media (max-width: $mobile-size-max) {
  //   margin-right: 17px;
  // }
}

