.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
}

.footer__social {
  width: 100%;
  height: 100%;
}

.footer__social-link:hover .footer__social,
.footer__social-link:focus .footer__social {
  fill: $java;
  stroke: $java;
}

.footer__social-link:active .footer__social {
  opacity: 0.5;
}

